*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: lightpink;

}

.App{
  text-align: center;
  width: 100%;
  height: 100%;
}


.header{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #334;
  height: 100px;
  color: orange;
}

nav{
  width: 550px;
  background-color: rgba(254, 254, 254, 0.1);
  border-radius: 10px;
  height: 25px;
  margin: 10px;
}
nav ul{
  display: flex;
  justify-content: space-around;
  list-style: none;
}
nav ul a{
  text-decoration: none;
  color: orange;
}


/*All members page*/
form{
  width: 500px;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 5px dotted indianred;
}
form > input {
  outline: none;
  height: 30px;
  width: 200px;
  border-radius: 5px;
  border: none;
}
form > label {
  padding: 10px 0 ;
}
form>div{
  display: flex;
  width: 150px;
  text-align: initial;
  justify-content: space-between;
  align-items: center;
}
form>div>div>label{
  padding-left: 5px;
}
.submitBtn{
  background-color: limegreen;
  margin: 15px;
  color: white;
}

table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  margin: 50px auto;
  width: 50%;
}

table th{
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #04AA6D;
  color: white;
}
table td, table th {
  border: 1px solid #ddd;
  padding: 8px;
}

table tr:nth-child(even){background-color: #f2f2f2;}
table tr:nth-child(odd){background-color: lavender;}


/*get member page*/
.getMemberByAllMeans{
  display: flex;
  justify-content: center;
}
.getMember > input {
  outline: none;
  height: 30px;
  width: 110px;
  border-radius: 5px;
  margin: 15px 0 10px 0;
  border: none;
}
.getMember{
  width: 250px;
}
#searchInfo{
  margin: 30px;
  font-family: cursive;
  font-size: 1.1rem;
}
#editBtn{
 background-color: royalblue;
 cursor: pointer;
 width: 42px;
 text-align: center;
 padding: 6px;
 border: none;
 outline: none;
 border-radius: 5px;
 color: white;
}
#delBtn{
  background-color: red;
  cursor: pointer;
  padding: 5px;
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;
}

/*modal*/
.modalBg{
  position: fixed; 
  padding-top: 50px;
  left: 0; 
  top: 0;
  width: 100%;
  height: 100%; 
  background-color: rgba(0, 0, 0, 0.3);
}
.modal{
  position: relative; 
  display: flex;
  justify-content: center;
  background-color: #f4f4f4;
  align-items: center;
  top: 0px;
  padding: 20px; 
  margin: 0px auto;
  width: 500px;
  height: fit-content;
  border-radius: 10px;
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.7s
}
.modalInner{
  background-color: #ebe1c1;
  height: 100%;
  width: 100%;
  margin-top: auto;
}
.modalInner>input {
  border: none;
  width: 90%;
  margin: 10px;
  height: 30px;
  outline: none;
  border-radius: 6px;
}

.modalInner>div{
  display: flex;
  width: 150px;
  text-align: initial;
  justify-content: space-between;
  align-items: center;
  margin:1.2em auto;
}
.closeBtn{
  display: block;
  width: 50px;
  margin: auto;
  padding: 10px;
  cursor: pointer;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
}
.editBtn{
  display: block;
  width:60px;
  text-align: center;
  margin: auto;
  padding: 10px;
  margin-left: 20px;
  cursor: pointer;
  background-color: dodgerblue;
  color: white;
  border: none;
  border-radius: 5px;
}
@keyframes animatetop {
  from {top:-300px; opacity:0}
  to {top:0; opacity:1}
}